.btn{
    font-weight: 600;
    font-size: 16px;
    display: block;
    height: 100%;
    padding-left: 40px;
    padding-right: 40px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
}

.btn-primary{
    color: #0e1c02;
    background: $primary;
    transition: all linear 200ms;

    &:hover{
        color: lighten($color: #0e1c02, $amount: 5);
    }
}

.btn-normal{
    border-radius: $radius;
    font-size: 14px;
    height: 50px;
    width: 300px;
}

.btn-primary-light{
    background: #fff;
    color: #0e1c02;
    text-transform: uppercase;

    &:hover{
        color: lighten($color: #0e1c02, $amount: 5);
    }
}
