a{
    text-decoration: none;
    color: #2A2A2A;
}

html, body{
    font-family: "Montserrat", sans-serif;
    background: #121212;
}

.logo{
    width: 50px;
    height: auto;
}

.white{
    color: white;
}

.title{
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
}

.subtitle{
    color: rgba(245, 245, 245, 0.6);
    line-height: 24px;
    font-size: 14px;
}

.divider{
    border-bottom: solid 0.1px $primary;

}

.mt-10{
    margin-top: 30px;
}

.flexed-form{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.d-none{
    display: none;
}

.column-form{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.badge{
    background: lighten($primary, 2);
    padding: 2px;
    border-radius: 4px;
    color: #2A2A2A;
}

.guihon-icon{
    *{
        font-size: 70px !important;
        color: $primary;
    }
}

.guihon-icon-error{
    *{
        font-size: 70px !important;
        color: #bc6c94;
    }
}

.flex-center{
    align-items: center !important;
    justify-content: center !important;
    flex-direction: column !important;
    gap: 20px !important
}

.center{
    text-align: center;
}

.mb100{
    padding-bottom: 80px !important;
}
